* {
  margin: 0px;
  padding: 0px;
  scroll-behavior: smooth;
  scroll-margin-top: 150px;
}

.App {
  width: 100%;
  background-color: #000;
  overflow-x: hidden;
}

.main-video-div {
  text-align: center;
}
.main-video-div .main-video {
  width: 70%;
  margin-top: 20vh;
  margin-bottom: 20vh;
  align-items: center;
  justify-content: center;
}

.instrument-button {
  width: 200px;
  height: 60px;
  border-radius: 50px;
  font-size: 1em;
  transition: all 0.4s ease;
  font-family: "Maven Pro", sans-serif;
  cursor: pointer;
}
.instrument-button:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.slides-main-div .slides-main-div-quote {
  width: 55%;
  margin: auto;
  padding-bottom: 200px;
}
.slides-main-div .slides-main-div-quote h2 {
  text-align: center;
  font-size: 4vw;
  font-family: "Maven Pro", sans-serif;
  color: #fff;
}
.slides-main-div .slides-of-assets {
  width: 85%;
  margin: auto;
}
.slides-main-div .slides-of-assets .asset-holder {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 100%;
  padding-bottom: 200px;
}
.slides-main-div .slides-of-assets .asset-holder .assets-photo {
  opacity: 0%;
  width: 50%;
  transition: all 0.7s ease;
}
.slides-main-div .slides-of-assets .asset-holder .text-about {
  width: 35%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 10%;
  opacity: 0%;
  transition: all 0.7s ease;
}
.slides-main-div .slides-of-assets .asset-holder .text-about h2 {
  width: 90%;
  color: #fff;
  font-family: "Maven Pro", sans-serif;
  font-size: 2vw;
}
.slides-main-div .slides-of-assets .asset-holder .show {
  opacity: 100%;
}
.slides-main-div .slides-of-assets .asset-holder-second {
  flex-direction: row-reverse;
}
.slides-main-div .slides-of-assets .asset-holder-second .text-about {
  margin-left: 5%;
}
.slides-main-div .slides-of-assets .asset-holder-second .text-about h2 {
  font-size: 2vw;
}
.slides-main-div .explore-our-work-div {
  width: 50%;
  padding-bottom: 300px;
  margin: auto;
  text-align: center;
}
.slides-main-div .explore-our-work-div h2 {
  color: #fff;
  font-family: "Maven Pro", sans-serif;
  font-size: 5vw;
  padding-bottom: 30px;
}

.company-helped-div {
  display: flex;
  flex-direction: row;
  width: 85%;
  margin: auto;
  gap: 5%;
}
.company-helped-div .company-helped {
  width: 50%;
  margin-bottom: 200px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
}
.company-helped-div .company-helped .photo-of-work {
  width: 100%;
}
.company-helped-div .company-helped .work-description {
  display: flex;
  flex-direction: row;
  text-align: left;
}
.company-helped-div .company-helped .work-description div, .company-helped-div .company-helped .work-description p {
  margin-top: 20px;
  flex: 1;
  color: #fff;
  font-family: "Maven Pro", sans-serif;
}
.company-helped-div .company-helped .work-description p {
  line-height: 1.6;
  font-size: 1.2vw;
}
.company-helped-div .company-helped .work-description h1 {
  width: 70%;
  font-size: 2.2vw;
}

.brands-icon-slider {
  width: 50%;
  margin: 200px auto 0px auto;
  padding-bottom: 500px;
  position: relative;
}
.brands-icon-slider .brand-icon-container {
  width: 100%;
  position: absolute;
  display: flex;
  gap: 20%;
}
.brands-icon-slider .brand-icon-container .brand-icon {
  width: 20%;
}
.brands-icon-slider .brand-icon-container .show {
  opacity: 100%;
  -webkit-animation-name: transitionShow;
          animation-name: transitionShow;
  -webkit-animation-duration: 0.15s;
          animation-duration: 0.15s;
}
.brands-icon-slider .brand-icon-container .unshow {
  opacity: 0%;
  -webkit-animation-name: transitionUnshow;
          animation-name: transitionUnshow;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}

@-webkit-keyframes transitionShow {
  from {
    opacity: 0%;
    transform: translateY(-40px);
  }
  to {
    opacity: 100%;
    transform: translateY(0px);
  }
}

@keyframes transitionShow {
  from {
    opacity: 0%;
    transform: translateY(-40px);
  }
  to {
    opacity: 100%;
    transform: translateY(0px);
  }
}
@-webkit-keyframes transitionUnshow {
  from {
    opacity: 100%;
    transform: translateY(0px);
  }
  to {
    opacity: 0%;
    transform: translateY(-40px);
  }
}
@keyframes transitionUnshow {
  from {
    opacity: 100%;
    transform: translateY(0px);
  }
  to {
    opacity: 0%;
    transform: translateY(-40px);
  }
}
.love-to-work-div {
  width: 80%;
  margin: auto;
  text-align: center;
  padding-bottom: 200px;
}
.love-to-work-div h1 {
  color: #fff;
  font-size: 4vw;
  font-family: "Maven Pro", sans-serif;
  margin-bottom: 40px;
}

.footer-div {
  display: flex;
  flex-direction: row;
  padding-bottom: 70px;
  width: 90%;
  margin: auto;
  justify-content: space-between;
}
.footer-div .media-part {
  margin-top: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-div .media-part button, .footer-div .media-part .footer-img {
  margin-left: 20px;
}
.footer-div .media-part .footer-button {
  font-family: "Maven Pro", sans-serif;
  background-color: #000;
  color: #fff;
  border: 2px white solid;
  border-radius: 40px;
  font-size: 1em;
  width: 200px;
  height: 60px;
  cursor: pointer;
  transition: all 0.4s ease;
}
.footer-div .media-part .footer-button:hover {
  border-color: rgb(151, 151, 151);
}
.footer-div .media-part .footer-img {
  width: 35px;
  height: 35px;
}
.footer-div .navbar-subsites {
  display: grid;
  grid-template-columns: 50% 50%;
  width: 300px;
}
.footer-div .navbar-subsites .subsite-text-container {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  color: #fff;
  font-family: "Maven Pro", sans-serif;
}
.footer-div .navbar-subsites .subsite-text-container .subsites-text {
  font-size: 0.94em;
  font-weight: 200;
  color: #fff;
  margin: 0px 10px 0px 10px;
}
.footer-div .navbar-subsites .subsite-text-container .subsites-text:hover {
  cursor: pointer;
}
.footer-div .navbar-subsites .subsite-text-container h3 {
  color: rgba(255, 255, 255, 0.76);
  font-size: 0.94em;
  font-weight: 200;
  margin: auto 0px auto 0px;
  text-align: center;
  opacity: 0%;
  transition: all 0.5s ease;
  cursor: default;
}
.footer-div .navbar-subsites .subsite-text-container .text-hovered {
  opacity: 100%;
}

@media only screen and (max-width: 1024px) {
  .company-helped-div {
    flex-direction: column;
  }
  .company-helped-div .company-helped {
    flex-direction: row;
    width: 100%;
    margin-bottom: 30px;
  }
  .company-helped-div .company-helped .photo-of-work {
    width: 50%;
  }
  .company-helped-div .company-helped .work-description {
    flex-direction: column;
    width: 100%;
    margin-left: 5%;
  }
  .company-helped-div .company-helped .work-description h1 {
    font-size: 3.5vw;
  }
  .company-helped-div .company-helped .work-description p {
    font-size: 1.5vw;
  }
}
@media only screen and (max-width: 768px) {
  .main-video-div {
    text-align: center;
  }
  .main-video-div .main-video {
    width: 90%;
  }
  .brands-icon-slider {
    width: 70%;
  }
  .slides-main-div .slides-of-assets .asset-holder {
    flex-direction: column;
  }
  .slides-main-div .slides-of-assets .asset-holder .assets-photo {
    width: 100%;
    transition: all 0.7s ease;
    margin-bottom: 30px;
  }
  .slides-main-div .slides-of-assets .asset-holder .text-about {
    width: 100%;
    margin: 0px;
  }
  .slides-main-div .slides-of-assets .asset-holder .text-about h2 {
    margin-bottom: 30px;
    font-size: 3vw;
  }
  .slides-main-div .slides-of-assets .asset-holder .text-about button {
    margin: auto;
  }
  .company-helped-div {
    flex-direction: column;
    width: 70%;
    margin: auto;
  }
  .company-helped-div .company-helped {
    flex-direction: column;
    width: 100%;
    margin-bottom: 30px;
  }
  .company-helped-div .company-helped .photo-of-work {
    width: 100%;
  }
  .company-helped-div .company-helped .work-description {
    flex-direction: column;
    width: 100%;
    margin-left: 0%;
  }
  .company-helped-div .company-helped .work-description h1 {
    font-size: 4.5vw;
  }
  .company-helped-div .company-helped .work-description p {
    font-size: 2.5vw;
  }
  .footer-div .navbar-subsites {
    width: 260px;
  }
  .footer-div .media-part {
    flex-direction: column;
  }
  .footer-div .media-part div {
    margin-top: 20px;
  }
}
@media screen and (max-width: 550px) {
  .footer-div {
    flex-direction: column;
  }
  .footer-div .navbar-subsites {
    margin: 0px auto 50px auto;
  }
  .footer-div .navbar-subsites .media-part {
    margin: auto;
  }
}
@media only screen and (max-width: 500px) {
  .brands-icon-slider {
    width: 85%;
  }
  .love-to-work-div h1 {
    font-size: 6vw;
  }
  .slides-main-div .slides-main-div-quote h2 {
    font-size: 6vw;
  }
  .slides-main-div .slides-of-assets {
    width: 85%;
  }
  .slides-main-div .slides-of-assets .asset-holder .text-about h2 {
    font-size: 5vw;
  }
  .company-helped-div {
    flex-direction: column;
    width: 85%;
    margin: auto;
    overflow-x: hidden;
  }
  .company-helped-div .company-helped .work-description h1 {
    font-size: 6.5vw;
  }
  .company-helped-div .company-helped .work-description p {
    font-size: 3.5vw;
  }
}/*# sourceMappingURL=App.css.map */